import React, { useEffect, useState } from "react";
import "../../../shared/settings.css";
import { GiSecurityGate } from "react-icons/gi";
import { BsQrCode } from "react-icons/bs";
import { TbFaceId } from "react-icons/tb";
import { GoLocation } from "react-icons/go";
import AdminSettingsHeader from "../../../components/AdminSettingsHeader";
import { FormControlLabel, FormGroup, Switch } from "@material-ui/core";
import { useGetSettingById, useGetSettings, useUpdateSettings } from "../hooks";
import { toast } from "react-toastify";
import { errorAlert, toastOptions } from "../../../utils";
import { useIsMutating } from "@tanstack/react-query";
import { Loader } from "../../../components";
import Layout from "../../../layout";
import { FaMicrosoft } from "react-icons/fa";

const Users = () => {
  const [facial, setFacial] = useState(false);
  const [QrCode, setQrCode] = useState(false);
  const [location, setLocation] = useState(false);
  const [auth, setAuth] = useState(false);
  const [formData, setFormData] = useState({});
  const isLoading = useIsMutating();

  const { data: settingsID } = useGetSettings();
  const ID = settingsID && settingsID[0].id;

  const { data } = useGetSettingById(ID);

  const { mutate, isError, isSuccess, reset, error } = useUpdateSettings();

  useEffect(() => {
    setQrCode(data?.data?.use_qr_code);
    setLocation(data?.data?.use_location);
    setFacial(data?.data?.use_facial_recognition);
    setAuth(data?.data?.use_ms_auth);
  }, [data]);

  if (isError) {
    reset();
    errorAlert(error);
  }
  if (isSuccess) {
    reset();
    toast.success(`Settings Updated`, toastOptions);
  }

  const qrHandler = () => {
    if (QrCode === true) {
      const newData = { ...formData, use_qr_code: false, id: data?.id };
      mutate(newData);
    } else {
      const newData = { ...formData, use_qr_code: true, id: data?.id };
      mutate(newData);
    }
  };

  const locationHandler = () => {
    if (location === true) {
      const newData = { ...formData, use_location: false, id: data?.id };
      mutate(newData);
    } else {
      const newData = { ...formData, use_location: true, id: data?.id };
      mutate(newData);
    }
  };

  const facialHandler = () => {
    if (facial === true) {
      const newData = {
        ...formData,
        use_facial_recognition: false,
        id: data?.id,
      };
      mutate(newData);
    } else {
      const newData = {
        ...formData,
        use_facial_recognition: true,
        id: data?.id,
      };
      mutate(newData);
    }
  };

  const authHandler = () => {
    if (auth === true) {
      const newData = { ...formData, use_ms_auth: false, id: data?.id };
      mutate(newData);
    } else {
      const newData = { ...formData, use_ms_auth: true, id: data?.id };
      mutate(newData);
    }
  };

  return (
    <Layout name="Settings" title="Settings">
      <div className="settings_container">
        <div className="settings_container_layout">
          <div className="setting_container">
            <AdminSettingsHeader authentication={"remote"} />
            <div className="title">
              <span className="ams__icon">
                <GiSecurityGate />
              </span>
              Authentication
            </div>
            {isLoading ? (
              <Loader />
            ) : (
              <div className="form_container">
                <div className="authentication_container">
                  <div className="icon_container">
                    <BsQrCode />
                  </div>
                  <div
                    style={{
                      fontWeight: "bold",
                      fontSize: ".8rem",
                    }}
                  >
                    {" "}
                    QR Code{" "}
                  </div>
                  <div className="toggle_container">
                    <FormGroup>
                      <FormControlLabel
                        control={<Switch defaultChecked />}
                        checked={QrCode}
                        label={QrCode ? "Enabled" : "Disabled"}
                        onChange={qrHandler}
                      />
                    </FormGroup>
                  </div>
                </div>
                <div className="authentication_container">
                  <div className="icon_container">
                    <GoLocation />
                  </div>
                  <div
                    style={{
                      fontWeight: "bold",
                      fontSize: ".8rem",
                    }}
                  >
                    {" "}
                    Enable Location{" "}
                  </div>
                  <div className="toggle_container">
                    <FormGroup>
                      <FormControlLabel
                        control={<Switch defaultChecked />}
                        checked={location}
                        label={location ? "Enabled" : "Disabled"}
                        onChange={locationHandler}
                      />
                    </FormGroup>
                  </div>
                </div>
                <div className="authentication_container">
                  <div className="icon_container">
                    <TbFaceId />
                  </div>
                  <div
                    style={{
                      fontWeight: "bold",
                      fontSize: ".8rem",
                    }}
                  >
                    {" "}
                    Facial Recognition{" "}
                  </div>
                  <div className="toggle_container">
                    <FormGroup>
                      <FormControlLabel
                        control={<Switch defaultChecked />}
                        checked={facial}
                        label={facial ? "Enabled" : "Disabled"}
                        onChange={facialHandler}
                      />
                    </FormGroup>
                  </div>
                </div>
                <div className="authentication_container">
                  <div className="icon_container">
                    <FaMicrosoft />
                  </div>
                  <div
                    style={{
                      fontWeight: "bold",
                      fontSize: ".8rem",
                    }}
                  >
                    {" "}
                    Microsoft Authentication
                  </div>
                  <div className="toggle_container">
                    <FormGroup>
                      <FormControlLabel
                        control={<Switch defaultChecked />}
                        checked={auth}
                        label={auth ? "Enabled" : "Disabled"}
                        onChange={authHandler}
                      />
                    </FormGroup>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Users;
